body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.main__title{font-size:24px}

.info-card{background-color:#fff;box-shadow:0 4px 10px rgba(0,0,0,0.1);border-radius:5px;padding:40px 30px}.info-card__title{padding-bottom:10px}

.custom-input__title{padding-bottom:10px}.custom-input__title-error{color:#ea522f}.custom-input__input{width:100%;height:40px;box-sizing:border-box;background-color:#fff;border-radius:5px;border:1px solid rgba(0,0,0,0.2);outline:none;padding:5px 10px;font-size:18px}.custom-input__input:hover{border:1px solid rgba(0,0,0,0.4)}.custom-input__input:focus{border:1px solid rgba(0,0,0,0.4)}.custom-input__input-error{border:1px solid #ea522f}.custom-input__input-error:hover,.custom-input__input-error:focus{border:1px solid #ea522f}.custom-input__error{padding-top:5px;color:#ea522f}

.lds-spinner{color:#fff;display:inline-block;position:relative;width:20px;height:20px}.lds-spinner div{transform-origin:10px 10px;-webkit-animation:lds-spinner 1.2s linear infinite;animation:lds-spinner 1.2s linear infinite}.lds-spinner div:after{content:' ';display:block;position:absolute;top:10px;left:16px;width:6px;height:2px;border-radius:5%;background:#fff}.lds-spinner div:nth-child(1){transform:rotate(0deg);-webkit-animation-delay:-1.1s;animation-delay:-1.1s}.lds-spinner div:nth-child(2){transform:rotate(30deg);-webkit-animation-delay:-1s;animation-delay:-1s}.lds-spinner div:nth-child(3){transform:rotate(60deg);-webkit-animation-delay:-0.9s;animation-delay:-0.9s}.lds-spinner div:nth-child(4){transform:rotate(90deg);-webkit-animation-delay:-0.8s;animation-delay:-0.8s}.lds-spinner div:nth-child(5){transform:rotate(120deg);-webkit-animation-delay:-0.7s;animation-delay:-0.7s}.lds-spinner div:nth-child(6){transform:rotate(150deg);-webkit-animation-delay:-0.6s;animation-delay:-0.6s}.lds-spinner div:nth-child(7){transform:rotate(180deg);-webkit-animation-delay:-0.5s;animation-delay:-0.5s}.lds-spinner div:nth-child(8){transform:rotate(210deg);-webkit-animation-delay:-0.4s;animation-delay:-0.4s}.lds-spinner div:nth-child(9){transform:rotate(240deg);-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.lds-spinner div:nth-child(10){transform:rotate(270deg);-webkit-animation-delay:-0.2s;animation-delay:-0.2s}.lds-spinner div:nth-child(11){transform:rotate(300deg);-webkit-animation-delay:-0.1s;animation-delay:-0.1s}.lds-spinner div:nth-child(12){transform:rotate(330deg);-webkit-animation-delay:0s;animation-delay:0s}@-webkit-keyframes lds-spinner{0%{opacity:1}100%{opacity:0}}@keyframes lds-spinner{0%{opacity:1}100%{opacity:0}}

.custom-button{height:40px;background:linear-gradient(180deg, #45A6FF 0%, #0055FB 100%),#C4C4C4;border-radius:5px;font-size:16px;padding:10px 30px;color:#fff}@media only screen and (max-width: 480px){.custom-button{width:100%}}.custom-button:hover{background:linear-gradient(0deg, rgba(255,255,255,0.15), rgba(255,255,255,0.15)),linear-gradient(180deg, #45A6FF 0%, #0055FB 100%),#C4C4C4}.custom-button:active{background:linear-gradient(0deg, rgba(0,0,0,0.15), rgba(0,0,0,0.15)),linear-gradient(180deg, #45A6FF 0%, #0055FB 100%),#C4C4C4}

.login-error-message{padding:10px;background:rgba(207,44,0,0.1);text-align:center;border-radius:5px;color:#cf2c00}

.sign-in{position:relative;height:100vh;padding:10px}.sign-in__row{padding:10px 0}.sign-in__card{width:500px;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);min-width:320px}@media only screen and (min-width: 481px) and (max-width: 767px){.sign-in__card{width:calc(100% - 20px)}}@media only screen and (max-width: 480px){.sign-in__card{width:calc(100% - 20px)}}

.custom-link{color:#999;text-decoration:none}.custom-link:hover{color:#736868}.custom-link:active{color:#463a3a}

.notification-window{background-color:#ea522f;padding:10px;box-shadow:0 4px 10px rgba(0,0,0,0.1);border-radius:5px}.notification-window__header{display:flex;justify-content:space-between;align-items:center}.notification-window__title{font-weight:bold;text-overflow:ellipsis;overflow:hidden;white-space:nowrap}.notification-window__close{padding-left:10px}.notification-window__message{padding-top:10px}

.notification-container{width:350px;position:absolute;right:10px;top:10px}@media only screen and (min-width: 481px) and (max-width: 767px){.notification-container{left:50%;transform:translateX(-50%);width:100%}}@media only screen and (max-width: 480px){.notification-container{left:50%;transform:translateX(-50%);width:100%}}.notification-container__item{padding:10px}

.connection-status{padding:10px;color:#fff;border-radius:5px;text-align:center}@media only screen and (max-width: 480px){.connection-status{width:100%}}.connection-status_connected{background:#0055fb}.connection-status_disconnected{background:#cf2c00}

.server-time{padding:10px;width:100%;border-radius:5px;border:1px solid rgba(0,0,0,0.2);text-align:center}.server-time_disconnected{background-color:rgba(207,44,0,0.1);color:rgba(175,141,141,0.5)}

.main{position:relative;height:100vh;padding:10px}.main__row{padding:10px 0}.main__git-row{display:flex;justify-content:center;padding-top:20px}.main__card{width:300px;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);min-width:320px}@media only screen and (min-width: 481px) and (max-width: 767px){.main__card{width:calc(100% - 20px)}}@media only screen and (max-width: 480px){.main__card{width:calc(100% - 20px)}}.main__row{display:flex;justify-content:center}

body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;background-color:#e5e5e5;color:#0d0d0d;font-size:16px}button{padding:0;margin:0;outline:transparent;background:transparent;border:0;cursor:pointer}ul{padding:0;margin:0;list-style-type:disc}li{display:block}h1{padding:0;margin:0}

